.career {
	&__content {
		.skills {
			display: flex;
			gap: 24px;
			margin: 24px 0;

			&__item {
				width: 100%;
				border: 1px solid $stroke;
				border-radius: 8px;
				padding: 12px 15px;
			}

			progress {
				width: 100%;
				margin-top: 6px;

			}
		}
	}

	&__tabcontent {
		display: none;

		&.active {
			display: block;
		}
	}
	&__tabs {
		margin-bottom: 32px;
		
		ul {
			margin: 0;
			padding: 0;
			list-style: none;
		}

		li {
			display: inline-block;
			padding: 0 16px 10px;
			margin: 0 32px 0 0;
			position: relative;
			
			a {
				color: $midgrey;
				font-size: 16px;
				line-height: 24px;
				letter-spacing: 0;
				transition: color .3s linear
			}
			
			&:after {
				display: block;
				border-radius: 2px;
				background-color: $primary;
				content: '';
				position: absolute;
				z-index: 100;
				width: 0;
				border: 1px solid $primary;
				height: 0;
				bottom: 0;
				left: 0;
				visibility: hidden;
				transition: width .3s linear, visibility .3s linear;
			}

			&:hover a {
				color: $midnight;
			}
			&.active {
				a {
					color: $midnight;
				}

				&::after {
					width: 100%;
					visibility: visible;
				}
			}
		}
		a {
			//border: 1px solid green;
		}
	}
}