.career__heading {
	background-color: $primary;
	padding-top: 19px;
	padding-bottom: 19px;
	margin-bottom: 80px;

	.heading__meta {
		display: flex;
		flex-direction: column;
		justify-content: center;
	}

	.breadcrumbs {
		ul {
			list-style: none;
			margin: 0;
			padding: 0;
		}

		li {
			display: inline-block;

			&:after {
				content: '/';
				opacity: 0.5;
				color: white;
			}

			&:last-child:after {
				content: none;
			}
		}

		a {
			font-weight: 500;
			letter-spacing: -0.01em;
			color: white;
		}
	}

	.location {
		color: rgba(255, 255, 255, .8);
		margin-top: 16px;
	}

	.container {
		display: flex;
		justify-content: space-between;
		align-items: stretch;
	}

	h2 {
		color: white;
		margin-top: 40px;
	}
}
