.refer {
	margin-bottom: 80px;
	.container {
		border-radius: 16px;
		border: 1px solid $stroke;
		padding: 40px;
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	button {
		color: $primary;
		background-color: white;
		font-size: 18px;
		line-height: 24px;
		font-weight: 800;
		border-radius: 50px;
		border: 1px solid $stroke;
		padding: 16px 64px;
	}

	p {
		font-size: 20px;
		line-height: 32px;
		color: $midgrey;
		margin-top: 0;
		margin-bottom: 8px;
	}
	strong {
		color: $midnight;
		font-size: 32px;
		line-height: 44px;
		font-weight: 900;
	}
}