.col-aside {
	.block {
		background-color: $alice_blue;
		border-radius: 16px;
		padding: 24px;
		margin-bottom: 24px;

		&__benefits {
			ul {
				list-style: none;
				padding-left: 1em;
				margin-bottom: 24px;
			}
			li {
				line-height: 24px;
				&::before {
					content: "•";
					color: $primary;
					display: inline-block;
					width: 1em;
					margin-left: -1em
				}
			}
			.bttn {
				width: 100%;
			}
		}

		&__share {
			ul {
				list-style: none;
				padding: 0;
				margin-bottom: 0;
			}

			li {
				margin-bottom: 12px;
				&:last-child {
					margin-bottom: 0;
				}
			}

			a {
				display: flex;
				align-items: center;
				font-weight: 500;
			}

			svg {
				margin-right: 12px;
			}
		}
	}
}