.apply {
	margin-bottom: 80px;
	.container {
		background-color: $alice_blue;
		border-radius: 16px;
		padding-top: 80px;
		padding-bottom: 80px;

		.row {
			max-width: 824px;
			padding-left: 24px;
			padding-right: 24px;
			margin: 24px auto;
		}
		.col-6 {
			&:first-child {
				padding-right: 12px;
			}

			&:last-child {
				padding-left: 12px;
			}
		}
	}

	h2 {
		text-align: center;
	}

	input {
		&[type="checkbox"] {
			margin-right: 12px;
		}

		&[type="submit"] {
			margin: 0 auto;
			display: block;
		}
	}

	textarea {
		height: 120px;
	}
	.dropzone {
		background-color: white;
		width: 100%;
		display: flex;
		border: 2px dashed $stroke;
		border-radius: 8px;
		height: 120px;
		justify-content: center;
		align-items: center;
		position: relative;

		&.file-dropping {
			background-color: $alice_blue;
		}

		span,
		input[type="file"] {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			opacity: 0;
			z-index: 110;
		}

		&__text {
			text-align: center;
			font-size: 14px;
			line-height: 20px;
			letter-spacing: -0.01em;
			strong {
				color: $primary;
				font-size: 16px;
				line-height: 24px;
			}
		}

	}

	.wpcf7-list-item {
		margin-left: 0;
	}


	label {
		color: $midgrey;
	}

	#file {
		opacity: 0;
		visibility: hidden;
	}
}